import React from 'react'

const PhotoHead = (props) => {
    return (
        <div>
        {
            Object.keys(props.item).length > 0 && (<img id="tw-photo-head" className="tw-head-photo client-photo" src={require('../../assets/gallery' + props.item.photo_url)} alt="tw-couple-title" />)
        }
        </div>
    )
}

export default PhotoHead
