const PhotoChild = (props) => {
    const [orientation, setOrientation] = useState('');

    const img = new Image();

    const setImage = (url) => {
        const imgUrl = require('../../assets/gallery' + url);
        img.onload = function () {
            if (img.width > img.height) {
                setOrientation('vertical');

                if (Number.isInteger(props.set / 2) === true) {
                    setOrientation('big');
                } else {
                    setOrientation('vertical');
                }

            } else if (img.width < img.height) {
                setOrientation('horizontal');
            }
        }
        img.src = imgUrl;
    }

    useEffect(() => {
        setImage(props.photo.photo_url);
    }, [props.photo.photo_url]);

    return (
        <div className={'tw-photo ' + orientation}>
            <img id="tw-photo-img" className="tw-photo-img" src={require('../../assets/gallery' + props.photo.photo_url)} alt="tw-couple-title" />
        </div>
    )
}

export default PhotoChild
