import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './styles/nav/_navigation.scss';
import { useSelector } from 'react-redux';

const Navigation = () => {
    const [menu, setMenu] = useState([]);
    const isGallery = useSelector(state => state.isGalleryNav);

    const fetchData = async () => {
        const res = await fetch(`/api/navigation`);
        res
            .json()
            .then(res => setMenu(res))
    };

    useEffect(() => {
        fetchData();
    }, [menu]);

    return (
        <div className={isGallery ? 'tw-nav' : 'tw-nav-front'}>
            <ul className="tw-main-nav">
                {
                    menu.map(menu => {
                        if (menu.id < 4) {
                            return (
                                <li key={menu.id} className={'menu' + menu.id}>
                                    <Link className="tw-nav-link" to={menu.nav_link}>
                                        {menu.nav_name}
                                    </Link>
                                </li>
                            )
                        }
                        return ''
                    })
                }
                <li><Link to="/"><img className="tw-main-logo" src={require('../assets/images/tw-logo-' + (isGallery ? 'black' : 'white') + '.png')} alt="tw-logo" /></Link></li>
                {
                    menu.map((menu) => {
                        if (menu.id > 3) {
                            return (
                                <li key={menu.id} className={'menu' + menu.id}>
                                    <Link className="tw-nav-link" to={menu.nav_link}>
                                        {menu.nav_name}
                                    </Link>
                                </li>
                            )
                        }
                        return ''
                    })
                }
            </ul>
        </div>
    )
}

export default Navigation
