import React, { useEffect } from 'react'
import Carousel from '../carousel/SlickCarousel';
import LatestGallery from './GalleryLatest';
import ContactForm from '../pages/ContactForm';
import {useSelector, useDispatch} from 'react-redux';
import { setNav } from '../../store/actions';

function FrontPage() {
  const dispatch = useDispatch();
  const isGallery = useSelector(state => state.isGalleryNav);

  const setNavigation = () => {
    isGallery ? dispatch(setNav()) : console.log('The Page is Front Page');
  }

  useEffect(() => {
    setNavigation();
  });

  
  return (
    <div className="tw-front-page">
      <Carousel />
      <LatestGallery />
      <ContactForm />
    </div>
  )
}

export default FrontPage;