import React, { useState, useEffect } from 'react'
import '../styles/gallery/_main_photo.scss';
import { useSelector, useDispatch } from 'react-redux';
import { setNav } from '../../store/actions';
import PhotoHead from './PhotoHead';
import PhotoThumb from './PhotoChild';

const Photo = ({ match }) => {
    const dispatch = useDispatch();
    const isGallery = useSelector(state => state.isGalleryNav);

    const setNavigation = () => {
        isGallery ? console.log(isGallery) : dispatch(setNav());
    };

    useEffect(() => {
        async function fetchData () {
            const res = await fetch(`/api/gallery/${match.params.child}/${match.params.client}`);
            res
                .json()
                .then(res => {
                    return setClientPhotos(res), setHeaderPhotos(res[5])
                })
        };
        fetchData();
        setNavigation();        
    }, [match.params.child, match.params.client]);
    
    let number = 0;
    const [clientPhotos, setClientPhotos] = useState([]);
    const [headerPhoto, setHeaderPhotos] = useState({});
    
    return (
        <div className="tw-client-photo">
            <PhotoHead item={headerPhoto}/>
            <h1> This Page is Client's Photo view </h1>
            <div className="photo-container">
            {
                clientPhotos.map(photos => {
                    number++;
                    return <PhotoThumb key={photos.id} photo={photos} set={number}/>
                })
            }
            </div>
        </div>
    )
}

export default Photo
