import React from 'react'
import Carousel from './FrontPage';
import Gallery from './Gallery';
import ClientPhoto from './Photo';
import { Switch, Route } from 'react-router-dom';

function index() {
    return (
        <div className="tw-page-list">
            <Switch>
                <Route path="/" exact component={Carousel} />
                <Route path="/gallery/:child/:client" component={ClientPhoto} />
                <Route path="/gallery/:child" component={Gallery} />
            </Switch>
        </div>
    )
}

export default index
