import React from 'react'
import './styles/footer/_footer.scss';

// <div className="tw-footer-children child-1">
//                 <img className="tw-carousel-img" src={require(`../assets/images/tw-logo-black.png`)} alt="tw-logo-black" />
//             </div>

const Footer = () => {
    return (
        <div className="tw-footer">
            <div className="tw-footer-children child-2">
                <div className="tw-sosial-text"><p>Spot me on :</p></div>
                <div className="tw-sosial-icons">
                    <div className="tw-sosial-icon icon-instagram">
                        <img className="tw-carousel-img" src={require(`../assets/images/icons/icon-instagram.svg`)} alt="tw-logo-black" />
                    </div>
                    <div className="tw-sosial-icon icon-facebook">
                        <img className="tw-carousel-img" src={require(`../assets/images/icons/icon-facebook.svg`)} alt="tw-logo-black" />
                    </div>
                </div>
            </div>
            <div className="tw-footer-children child-3">
                <Line color="rgba(33,33,33,0.12)" />
                <p>&copy; 2019 | TW Photography. All rights reserved.</p>
            </div>
        </div>
    )
}

const Line = ({ color }) => {
    return (
        <div className="tw-footer-line">
            <hr
                style={{
                    backgroundColor: color,
                    height: 1
                }}
            />
        </div>

    )
}

export default Footer
