import React, { useState, useEffect } from 'react'
import '../styles/gallery/_main_gallery.scss';
import GallerySide from './GalleryChild';
import { useSelector, useDispatch } from 'react-redux';
import { setNav } from '../../store/actions';

const Gallery = ({ match }) => {
    const dispatch = useDispatch();
    const isGallery = useSelector(state => state.isGalleryNav);

    const setNavigation = () => {
        isGallery ? console.log(isGallery) : dispatch(setNav());
    };

    useEffect(() => {
        async function fetchData () {
            const res = await fetch(`/api/gallery/${match.params.child}`);
            res
                .json()
                .then(res => setClientGallery(res))
        };

        fetchData();
        setNavigation();
    }, [match.params.child]);
    
    let number = 0;
    const [clientGallery, setClientGallery] = useState([]);

    console.log("API Couple Gallery ", clientGallery);
    return (
        <div className="tw-gallery tw-gallery-couple">
            <div className="tw-gallery-title">
                <img className="tw-couple-title" src={require('../../assets/gallery/titles/title-couple.png')} alt="tw-couple-title" />
            </div>
            {
                clientGallery.map(gallery => {
                    number++;
                    return <GallerySide key={gallery.id} gallery={gallery} reverse={Number.isInteger(number / 2) ? true : false} />

                })
            }
        </div>
    )
}

export default Gallery
