import React, { Component } from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../styles/carousel/_carousel.scss";

const SetRightArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <img className={className + ' tw-carousel-arrow right'}  src={require(`../../assets/images/icons/arrow-right.png`)} alt="tw-carousel-arrow" style={{...style}} onClick={onClick}/>
    )
}

const SetLeftArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <img className={className + ' tw-carousel-arrow left'}  src={require(`../../assets/images/icons/arrow-left.png`)} alt="tw-carousel-arrow" style={{...style}} onClick={onClick}/>
    )
}

export default class SlickCarousel extends Component {
    constructor() {
        super();
        this.state = {
            images: []
        }
    }

    componentDidMount() {
        fetch('/api/carouselimg')
            .then(res => res.json())
            .then(images => this.setState({ images }, () => console.log('API Carousel ',this.state.images)));
    }

    render() {
        var settings = {
            dots: true,
            infinite: true,
            swipeToSlide: true,
            speed: 2000,
            autoplay: true,
            autoplaySpeed: 6000,
            draggable: false,
            swipe: false,
            nextArrow: <SetRightArrow />,
            prevArrow: <SetLeftArrow />,
        };
        return (
            <div className="tw-carousel-container">
                <Slider {...settings}>
                    {
                        this.state.images.map((image) =>
                            <div key={image.id.toString()}>
                                <img className="tw-carousel-img"  src={require(`../../assets/${image.img_url}`)} alt="tw-carousel-img"/>
                            </div>
                        )
                    }
                </Slider>
            </div>
        )
    }
}
